import React from "react";
import style from './team.module.scss';
import team_0 from '../../assets/jpeg/team_0.jpg';
import team_1 from '../../assets/jpeg/team_1.jpg';
import team_2 from '../../assets/jpeg/team_2.jpg';
import team_3 from '../../assets/jpeg/team_3.jpg';
import team_4 from '../../assets/jpeg/team_4.jpg';
import team_5 from '../../assets/jpeg/team_5.jpg';
import team_6 from '../../assets/jpeg/team_6.jpg';
import team_7 from '../../assets/jpeg/team_7.jpg';
import back from '../../assets/jpeg/team_back.jpg';
import {SvgIcon} from "../../common/SvgIcon/SvgIcon";
import {svgIcons} from "../../assets/svg/svgIcons";


const team = [
    {
        src: team_0,
        name: 'Haiden Rivera',
        job: 'Haiden Rivera',
        mail: '#',
        in: '#'
    },
    {
        src: team_1,
        name: 'Ashanti Steele',
        job: 'SEO & founder',
        mail: '#',
        in: '#'
    },
    {
        src: team_2,
        name: 'Kester Barnett',
        job: 'SEO & founder',
        mail: '#',
        in: '#'
    },
    {
        src: team_3,
        name: 'Chantal Wickens',
        job: 'SEO & founder',
        mail: '#',
        in: '#'
    },
    {
        src: team_4,
        name: 'King Crane',
        job: 'SEO & founder',
        mail: '#',
        in: '#'
    },
    {
        src: team_5,
        name: 'Syed Mcclain',
        job: 'SEO & founder',
        mail: '#',
        in: '#'
    },
    {
        src: team_6,
        name: 'Raheem Farrow',
        job: 'SEO & founder',
        mail: '#',
        in: '#'
    },
    {
        src: team_7,
        name: 'Edwin Douglas',
        job: 'SEO & founder',
        mail: '#',
        in: '#'
    },
];

const column1 = [team[0], team[2],team[4],team[6]];
const column2 = [team[1], team[3],team[5],team[7]];
const columnDesktop_0 = [team[0], team[4]];
const columnDesktop_1 = [team[1], team[5]];
const columnDesktop_2 = [team[2], team[6]];
const columnDesktop_3 = [team[3], team[7]];
const columnsDesktop = [columnDesktop_0, columnDesktop_1, columnDesktop_2, columnDesktop_3];


export const Team = () => {
    return (
        <section className={style.team}>
            <div className={style.inner}>

                <h2 className={style.title}>Team and advisors</h2>

                <div className={style.team_mobile}>
                    <div className={style.column}>
                        {
                           column1.map((el, i) => (
                               <div className={style.card} key={i}>
                                   <div className={style.border}/>
                                    <div className={style.card_inner}>
                                        <img className={style.back} src={back} alt=""/>
                                        <div className={style.content}>
                                            <img className={style.photo} src={el.src} alt=""/>
                                            <div className={style.name}>{el.name}</div>
                                            <div className={style.job}>{el.job}</div>
                                            <div className={style.social}>
                                                <a className={style.link}
                                                    href={el.mail}>
                                                    <SvgIcon icon={svgIcons.mail}/>
                                                </a>
                                                <a className={style.link}
                                                   href={el.in}>
                                                    <SvgIcon icon={svgIcons.in}/>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                               </div>
                           ))
                        }
                    </div>
                    <div className={style.column}>
                        {
                            column2.map((el, i) => (
                                <div className={style.card} key={i}>
                                    <div className={style.border}/>
                                    <div className={style.card_inner}>
                                        <img className={style.back} src={back} alt=""/>
                                        <div className={style.content}>
                                            <img className={style.photo} src={el.src} alt=""/>
                                            <div className={style.name}>{el.name}</div>
                                            <div className={style.job}>{el.job}</div>
                                            <div className={style.social}>
                                                <a className={style.link}
                                                   href={el.mail}>
                                                    <SvgIcon icon={svgIcons.mail}/>
                                                </a>
                                                <a className={style.link}
                                                   href={el.in}>
                                                    <SvgIcon icon={svgIcons.in}/>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>

                <div className={style.team_desktop}>
                    {
                        columnsDesktop.map((column, i) => (
                            <div className={style.column}
                                 key={i}
                                 style={{
                                     marginTop: `${i * 60}px`
                                 }}
                            >
                                {
                                    column.map((el, i) => (
                                        <div className={style.card} key={i}>
                                            <div className={style.border}/>
                                            <div className={style.card_inner}>
                                                <img className={style.back} src={back} alt=""/>
                                                <div className={style.content}>
                                                    <img className={style.photo} src={el.src} alt=""/>
                                                    <div className={style.name}>{el.name}</div>
                                                    <div className={style.job}>{el.job}</div>
                                                    <div className={style.social}>
                                                        <a className={style.link}
                                                           href={el.mail}>
                                                            <SvgIcon icon={svgIcons.mail}/>
                                                        </a>
                                                        <a className={style.link}
                                                           href={el.in}>
                                                            <SvgIcon icon={svgIcons.in}/>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        ))

                    }
                </div>

            </div>
        </section>
    )
};